import fetch from '@/utils/fetch'

export function getApproval(query) {
    return fetch({
        url: 'admin/withdraw/approval',
        method: 'get',
        params: query
    })
}

export function passApproval(query) {
    return fetch({
        url: 'admin/withdraw/passapproval',
        method: 'put',
        data: query
    })
}

export function cancelApproval(query) {
    return fetch({
        url: 'admin/withdraw/cancelapproval',
        method: 'put',
        data: query
    })
}

export function lockApproval(query) {
    return fetch({
        url: 'admin/withdraw/lockapproval',
        method: 'put',
        data: query
    })
}

export function issueApproval(query) {
    return fetch({
        url: 'admin/withdraw/issueapproval',
        method: 'put',
        data: query
    })
}

export function rmbquery(query) {
    return fetch({
        url: 'admin/rmbtransactions/query',
        method: 'post',
        data: query
    })
}

export function goodsquery(query) {
    return fetch({
        url: '/admin/applydeliverygoods/query',
        method: 'post',
        data: query
    })
}

export function getexpress() {
    return fetch({
        url: '/admin/getexpresslist',
        method: 'get',
    })
}

export function reviewgoods(query) {
    return fetch({
        url: '/admin/applydeliverygoods/review',
        method: 'put',
        data: query
    })
}

export function lockgoods(id, status) {
    return fetch({
        url: '/admin/applydeliverygoods/' + id + '/' + status + '/lock',
        method: 'put'
    })
}

export function cancelgoods(id) {
    return fetch({
        url: '/admin/applydeliverygoods/' + id + '/cancel',
        method: 'put'
    })
}

export function issuegoods(id, type) {
    return fetch({
        url: '/admin/applydeliverygoods/' + id + '/' + type + '/issue',
        method: 'put'
    })
}

export function postexpress(query) {
    return fetch({
        url: '/admin/postexpress',
        method: 'post',
        data: query
    })
}

export function getgoodslist(query) {
    return fetch({
        url: 'admin/virtualcoinandgoods',
        method: 'put',
        data: query
    })
}



