<template>
    <div class="p-20">

        <div class="filter-container" style="margin-bottom:20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入用户ID、登录名"
                      v-model="currencyTrans.keyword">
            </el-input>
            <el-button class="filter-item" @click="conditionQuery" style="margin-left: 30px">查询
            </el-button>
            <el-button type="primary" v-if="permission.export" @click="derivedData" style="margin-left: 100px">导出数据
            </el-button>
        </div>
        <div>
            <el-table
                    ref="singleTable"
                    :data="tableData"
                    v-loading="listLoading"
                    element-loading-text="给我一点时间">
                <el-table-column
                        v-for="item in tableHeader"
                        :key="item.prop"
                        :label="item.label"
                        :prop="item.prop"
                        :width="item.width"
                        :min-width="item.minWidth"
                        :align="item.align"
                        :formatter="item.formatter"
                        show-overflow-tooltip>
                </el-table-column>
                <el-table-column
                        fixed="right"
                        label="操作"
                        width="200">
                    <template scope="scope" class="filter-operation">
                        <ul style="list-style: none;padding: 0px;height: 30px;margin: 0px">
                            <li style="float:left;width: 100px"
                                v-if="scope.row.accountStatus==6">
                                <el-button
                                        type="text"
                                        @click="passApproval(scope.row.id,3)">审核
                                </el-button>
                            </li>
                            <li style="float:right;width: 60px">
                                <el-button
                                        type="text"
                                        @click="cancelApproval(scope.row.id)">取消提现
                                </el-button>
                            </li>
                            <li style="float:right;width: 60px"
                                v-if="scope.row.accountStatus===6">
                                <el-button
                                        type="text"
                                        @click="lockApproval(scope.row.id,2)">取消锁定
                                </el-button>
                            </li>
                            <li style="float:right;width: 100px"
                                v-else-if="scope.row.accountStatus!==6">
                                <el-button
                                        type="text"
                                        @click="lockApproval(scope.row.id,6)">锁定
                                </el-button>
                            </li>
                            <li style="float:right;width: 100px"
                                v-if="scope.row.accountStatus===6">
                                <el-button
                                        type="text"
                                        @click="issueApproval(scope.row.id,3)">移入问题列表
                                </el-button>
                            </li>
                        </ul>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination @size-change="userPageSize" @current-change="userPage"
                               :current-page="currencyTrans.page"
                               :page-sizes="[10,20,30, 50]" :page-size="currencyTrans.pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    /*import fetch from '@/utils/fetch'*/
    import DCTP from '@/utils/dctp';
    import {getApproval, passApproval, cancelApproval, lockApproval, issueApproval} from '@/api/awaits';
    //启用禁用筛选条件
    const allCoin = {baseSeq: 0, chineseName: '全部类型', englishName: ""};
    const statuScope = [
        {key: 0, display_name: '全部状态'},
        {key: 2, display_name: '等待审核'},
        {key: 3, display_name: '成功提现'},
        {key: 4, display_name: '系统撤销'},
        {key: 5, display_name: '用户撤销'},
        {key: 6, display_name: '正在处理'},
    ];

    export default {
        data() {
            return {
                //搜索需求数据等
                allCoin,
                statuScope,
                coinList: [],
                dateTime: '',
                tableData: [],
                total: null,
                lockWithdrawalVo: {
                    id: '',
                    status: ''
                },
                currencyTrans: {
                    keyword: '',
                    page: 1,
                    pageSize: 10
                },
                msg: '',
                listLoading: true,
                tableHeader: [
                    {
                        label: "单号id",
                        prop: "id",
                        width: 90
                    },
                    {
                        label: "会员UID",
                        prop: "userId",
                        width: 100
                    }, {
                        label: "登录名",
                        prop: "phoneNum",
                        width: 100
                    }, {
                        label: "会员昵称",
                        prop: "nickName",
                        width: 125
                    }, {
                        label: "真实姓名",
                        prop: "currencyName",
                        width: 110
                    }, {
                        label: "状态",
                        prop: "accountStatus",
                        width: 125,
                        align: 'center',
                        formatter(row, column, value) {
                            let item = DCTP.utils.findDataByValue(statuScope, value, 'key');
                            return item ? item.display_name : '';
                        }
                    }, {
                        label: "提现金额",
                        prop: "actualmoney",
                        width: 95,
                        align: 'center'
                    }, {
                        label: "手续费",
                        prop: "chargefee",
                        width: 110
                    }, {
                        label: "银行",
                        prop: "bankName",
                        width: 120,
                        align: 'center'
                    }, {
                        label: "提现账号",
                        prop: "account",
                        width: 220,
                        align: 'center',
                        formatter(row, column, value) {
                            if (row.accountStatus !== 6) {
                                return "********";
                            }
                            return row.account;
                        }
                    }, {
                        label: "创建时间",
                        prop: "createTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }, {
                        label: "最后修改时间",
                        prop: "updateTime",
                        width: 170,
                        formatter(row, column, value) {
                            return DCTP.utils.parseDateTimeStr(value)
                        }
                    }]
            }
        }
        ,
        computed: {
            tableDataLength() {
                return this.tableData.length;
            }
        }
        ,
        methods: {


            //获取当前页数
            userPage(val) {
                this.currencyTrans.page = val;
                this.getList();
            }
            ,
            //获取每页多少条
            userPageSize(val) {
                this.currencyTrans.pageSize = val;
                this.getList();
            }

            ,
            //条件查询方法
            conditionQuery() {
                this.getList();
            },
            /**
             * 移入问题列表方法
             * @param id
             * @param status
             */
            issueApproval(id, status) {
                this.lockWithdrawalVo.id = id;
                this.lockWithdrawalVo.status = status;
                issueApproval(this.lockWithdrawalVo).then(response => {
                    if (response.result) {
                        this.msg = "已移入问题列表";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    } else {
                        this.msg = "移入问题列表失败";
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 锁定订单方法
             * @param id
             * @param status
             */
            lockApproval(id, status) {
                this.lockWithdrawalVo.id = id;
                this.lockWithdrawalVo.status = status;
                lockApproval(this.lockWithdrawalVo).then(response => {
                    if (response.result) {
                        this.msg = "已锁定此单";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    } else {
                        this.msg = "未能锁定请检查";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 取消提现方法
             * @param id
             */
            cancelApproval(id) {
                this.lockWithdrawalVo.id = id;
                cancelApproval(this.lockWithdrawalVo).then(response => {
                    if (response.result) {
                        this.msg = "已取消提现";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    }
                    this.getList();
                })
            }
            ,
            /**
             * 审核通过方法
             * @param id
             * @param status
             */
            passApproval(id, status) {
                this.lockWithdrawalVo.id = id;
                this.lockWithdrawalVo.status = status;
                passApproval(this.lockWithdrawalVo).then(response => {
                    if (response.result) {
                        this.msg = "审核通过";
                        this.$message({
                            type: 'success',
                            message: this.msg
                        });
                    } else {
                        this.msg = response.msg;
                        this.$message({
                            type: 'error',
                            message: this.msg
                        });
                    }
                    this.getList();
                })

            }
            ,
            /**
             * 请求列表方法
             */
            getList() {
                this.listLoading = true;
                getApproval(this.currencyTrans).then(response => {
                    this.tableData = response.data.list;
                    this.total = response.data.total;
                    this.coinList.unshift(this.allCoin);
                    if (!response.result) {
                        this.msg = response.msg;
                        this.$message({
                            type: 'warning',
                            message: this.msg
                        });
                    }
                    this.listLoading = false;
                })
            }
        }
        ,
        created() {
            this.getList();
        }
    }
</script>



